<template>
    <div class="manage">
        <van-nav-bar
        title="地址管理"
        left-text="返回"
        left-arrow
        @click-left="black"
        />
        <BgBox>        
            <van-address-list
            v-model="chosenAddressId"
            :list="addressList"
            default-tag-text="默认"
            @add="onAdd"
            @edit="onEdit"
            />
        </BgBox>

    </div>
</template>

<script>
    import BgBox from "../components/BgBox.vue";
    export default {
        name: 'ManageAddress',
        components: {
            BgBox
        },
        data(){
            return {
                chosenAddressId: '1',
                addressList: [],
                aid: []
            }
        },
        created(){
            this.findAddress();
        },
        methods: {
            onAdd() {
                this.$router.push({name: 'NewAddress'});
            },
            black(){
                this.$router.go(-1);
            },
            onEdit(item) {
                this.$router.push({name: 'NewAddress',query:{aid: item.aid}});
            },
            // 获取地址数据
            findAddress(){
                let tokenString = localStorage.getItem('token');
                if(!tokenString){
                    this.$toast("请登录");
                    return this.$router.push({name:'Login'});
                }
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0,
                })
                this.axios({
                    method: 'GET',
                    url: '/findAddress',
                    params:{
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 20000){
                        res.data.result.map((v,index) => {
                            v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`;
                            this.addressList.push(v);                          
                        })
                    }
                }).catch(err => {
                    this.$toast.clear();
                })
            }
        },
    }
</script>

<style lang="less" scoped>
.manage{
    .van-address-list__bottom{
        bottom: 20px;
    }
    .van-radio__icon {
        display: none;
    }
     /deep/.van-button--danger {
        background: linear-gradient(to right, #ff6034, #ee0a24);
    }
}
   
</style>